import React, { Component } from 'react';
import './index.css';
import ImageColumn from '../ImageColumn';

// Елементи з БД і їх обчислення
import _ from 'underscore';

class Images extends Component {  
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {  dataUrl: [], show: false };
  };
  
  componentDidMount() {   
    if( this.props.urls.length > 0 ) {
      console.log( this.props.urls.length + ' > 0' );
      let ff = _.chunk( this.props.urls, this.props.urls.length / 4 );
      this.setState({ dataUrl: ff, show: true });
    };
  };

  render() {       
    const { dataUrl } = this.state;                
    return (
      <div>        
          { 
            this.state.show 
            ?
            <div className="row">
              <ImageColumn dataurl={ dataUrl[0] } />
              <ImageColumn dataurl={ dataUrl[1] } />
              <ImageColumn dataurl={ dataUrl[2] } />
              <ImageColumn dataurl={ dataUrl[3] } />         
            </div>
        
            :<div>LOADING...</div>
          }
      </div>
    );
  };
};
  
export default Images;

