import React, { Component } from 'react';
import './index.css';

import logo from '../../loading.svg';

export default class Loading extends Component {  
    render() {
      return (
        <div className="parentLoading">
          <div className="blockLoading">
              <img src={logo} alt='pic' />
          </div>
        </div>
      )
    }
};


